import { ChannelError } from './errors.js';

/** Base class for the signaling channels used to create a WebRTC conection. */
export class SignalingChannel {
	/** Create a new signaling channel */
	constructor() {
		/**
		 * Object to store and lookup event listeners by name
		 * @type {Object.<string, function[]>}
		 * @private
		 */
		this._eventListeners = {};

		/**
		 * Proxy the event to all event handlers listening for events of the event's type
		 * @param {Object} event The event being raised
		 * @param {String} event.type The type of the event
		 * @private
		 */
		this._dispatchEvent = (event) => {
			if (event.type in this._eventListeners) {
				this._eventListeners[event.type].forEach((listener) => listener.call(event.source, event));
			}
		};

		/**
		 *
		 * @type {null}
		 * @private
		 */
		this._channel = null;
	}

	/**
	 * Add a listener for an event that could be raised by the underlying communication channel
	 * @param {String} type The name of the event
	 * @param {Function} handler The function to remove
	 */
	addEventListener(type, handler) {
		if (!(type in this._eventListeners)) {
			this._eventListeners[type] = [];
		}

		this._eventListeners[type].push(handler);
	}

	/**
	 * Remove a listener from an event that could be raised by the underlying communication channel
	 * @param {String} type The name of the event
	 * @param {Function} handler The function to remove
	 */
	removeEventListener(type, handler) {
		if (type in this._eventListeners) {
			this._eventListeners[type] = this._eventListeners[type].filter((element) => element !== handler);
		}
	}

	/**
	 * Attach the event handlers to the events of the web socket
	 * @private
	 */
	_setupEventProxies() {
		this._channel.addEventListener('close', this._dispatchEvent);
		this._channel.addEventListener('error', this._dispatchEvent);
		this._channel.addEventListener('message', this._dispatchEvent);
	}

	/**
	 * Disconnect the signaling channel, aborting any connection attempt in progress
	 * @param {Object} message The message being sent to negotiate the peer connection
	 */
	send(message) { // eslint-disable-line no-unused-vars
		throw new Error('function "send(message)" needs to be implemented by derived class');
	}

	/**
	 * Complete any final cleanup. Call this function when the connection has been made.
	 */
	finalize() {} // eslint-disable-line no-empty-function
}

SignalingChannel.WebSocket = class extends SignalingChannel {
	/**
	 * Create a WebSocket based signaling channel
	 * @param {String} host The host address of the server
	 */
	constructor(host) {
		super();

		/**
		 * The host address of the WebSocket Server
		 * @type {String}
		 */
		this.host = host;
	}

	/**
	 * Open a connection between this object and the signaling server
	 * @param {Object} cancelToken An object that will be used in allowing asynchronous cancel
	 * @param {Function} cancelToken.cancel  After the function returns, this field will contain a function that can be called to cancel the connection attempt
	 * @param {Object} [options] Connection options
	 * @param {Number} [options.timeout] The time (in milliseconds) each try of a connection can wait before being considered unsuccessful
	 * @returns {Promise} A Promise that resolves when the web socket to the server has been opened
	 */
	connect(cancelToken, options) {
		const timeout = (options && options.timeout) || 1000;
		this._connectionPromise = this._connectionPromise || new Promise((accept, reject) => {
			const setupConnectionListeners = () => {
				this._channel.addEventListener('open', handleOpen); // eslint-disable-line no-use-before-define
				this._channel.addEventListener('close', handleClose); // eslint-disable-line no-use-before-define
				this._channel.addEventListener('error', handleError); // eslint-disable-line no-use-before-define
			};

			const tearDownConnectionListeners = () => {
				if (this._channel) {
					this._channel.removeEventListener('open', handleOpen); // eslint-disable-line no-use-before-define
					this._channel.removeEventListener('close', handleClose); // eslint-disable-line no-use-before-define
					this._channel.removeEventListener('error', handleError); // eslint-disable-line no-use-before-define
				}
			};

			const handleOpen = () => {
				clearTimeout(timeoutID); // eslint-disable-line no-use-before-define
				tearDownConnectionListeners();
				this._setupEventProxies();
				cancelToken.cancel = null;
				accept();
			};

			const cancel = (reason) => {
				this._connectionPromise = null;
				clearTimeout(timeoutID); // eslint-disable-line no-use-before-define
				tearDownConnectionListeners();
				if (this._channel) {
					this._channel.close();
				}

				reject(reason);
				this._dispatchEvent({
					type: 'error',
					reason,
				});
			};

			const handleClose = () => {
				tearDownConnectionListeners();
				reject(ChannelError.FailToCreate);
			};

			const handleError = () => {
				/*
					does nothing but swallow web socket exceptions
					we handle these in the close event anyway.
				 */
			};

			let timeoutID;

			if (timeout) {
				timeoutID = setTimeout(() => cancel(ChannelError.Timeout), timeout);
			}

			if (this._channel && this._channel.readyState === 1) {
				cancel(ChannelError.ChannelExists);
			} else {
				try {
					this._channel = new WebSocket(this.host);
					setupConnectionListeners();
				} catch (err) {
					reject(ChannelError.FailToCreate);
				}
			}

			cancelToken.cancel = cancel;
		});

		return this._connectionPromise;
	}

	/**
	 * Make sure that the connection to the signaling server has been shut down
	 */
	disconnect() {
		// _channel can be null if the connection was not correctly set up in the first place
		if (this._channel) {
			this._channel.close();
			this._channel = null;
		}

		this._connectionPromise = null;
	}

	/** @override */
	send(message) {
		if (this._channel.readyState === 1) {
			this._channel.send(message);
		}
	}
};

SignalingChannel.MessageChannel = class extends SignalingChannel {
	/**
	 * Create a signaling channel that works over a message channel through another web page
	 * @param {String} url The URL of the page that will be the proxy
	 * @param {String} windowName Then name for the window - used to identify the window (for example for window reuse)
	 * @param {String} windowFeatures A string describing any properties of the proxy window to be opened
	 * @param {String} [deviceUrl] The address at which the device is exposing its signaling port
	 */
	constructor(url, windowName, windowFeatures, deviceUrl) {
		super();

		/**
		 * The url of the proxying web page
		 * @type {String}
		 */
		this.url = url;

		this.windowFeatures = windowFeatures;

		/**
		 * If defined, the URL at which the device has its signaling socket
		 * @type {String}
		 */
		this.deviceUrl = deviceUrl;

		this.windowName = windowName || 'Connection Status';
	}

	/**
	 * Connect the signaling channel through the proxy page
	 * @param {Object} cancelToken An object that will used for asynchronous cancel
	 * @param {Function} cancelToken.cancel  After the function returns, this field will contain a function that can be called to cancel the connection attempt
	 * @param {Object} [options] Connection options
	 * @param {Number} [options.timeout=20000] The time (in milliseconds) each try of a connection can wait before being considered unsuccessful
	 * @returns {Promise} A Promise that resu
	 */
	connect(cancelToken, options) {
		const timeout = (options && options.timeout) || 20000;
		this._connectionPromise = this._connectionPromise || new Promise((accept, reject) => {
			let href = `${this.url}?origin=${document.location.origin}`;

			if (this.deviceUrl) {
				href += `&deviceServer=${this.deviceUrl}`;
			}

			this._proxyWindow = window.open(href, this.windowName, this.windowFeatures);

			let timeoutID = setTimeout(() => {
				cancel(ChannelError.Timeout); // eslint-disable-line no-use-before-define
			}, timeout);

			const cancel = (reason) => {
				this._connectionPromise = null;

				window.removeEventListener('message', this._handleMessage);
				clearTimeout(timeoutID);

				if (this._channel) {
					this._channel.close();
					this._channel = null;
				}

				if (this._proxyWindow) {
					this._proxyWindow.close();
					this._proxyWindow = null;
				}

				reject(reason);
				this._dispatchEvent({
					type: 'error',
					reason,
				});
			};

			this._handleMessage = (messageEvent) => {
				switch (messageEvent.data) {
				case 'createChannel':
					if (messageEvent.origin === new URL(this.url).origin && messageEvent.source === this._proxyWindow) {
						clearTimeout(timeoutID);
						timeoutID = null;
						this._channel = messageEvent.ports[0];
						this._setupEventProxies();
						this._channel.start();
						cancelToken.cancel = null;
						accept();
					}
					break;

				case 'closing':
					if (messageEvent.origin === new URL(this.url).origin
						&& (messageEvent.source === this._proxyWindow || messageEvent.source === undefined)) {
						cancel(ChannelError.Canceled);
					}
					break;
				}
			};

			window.addEventListener('message', this._handleMessage);

			cancelToken.cancel = cancel;
		});

		return this._connectionPromise;
	}

	/** Disconnect is a no-op for this channel. Once the connection has been established, the window gets closed anyway,
	 * which means the channel is useless.
	 */
	disconnect() { // eslint-disable-line no-empty-function
	}

	/**
	 * @override
	 */
	send(message) {
		this._channel.postMessage(message);
	}

	/**
	 * @override
	 */
	finalize() {
		window.removeEventListener('message', this._handleMessage);
		this._proxyWindow.postMessage('connected', new URL(this.url).origin);
		this._channel.close();

		setTimeout(() => {
			this._proxyWindow.close();
			this._proxyWindow = null;
		}, 3000);

		this._connectionPromise = null;
	}
};
