<template>
  <div id="app">
		<div class="sfp-title">
			<h3 class="status">DSDB&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{status}}</h3>
		</div>
		<div class="control">
			<img id="logo" alt="Vue logo" src="./assets/logo.png">
			<input class="device-id"
        type="text"
        name="Device Code"
        placeholder="Input device code here"
        v-model="deviceCode">
			<button class="connect btn" 
        :disabled="this.status==='Connecting'"
        @click="onconnect">
        {{connectBtnText}} 
      </button>
			<button class="run btn"
        @click="onrun"
        :disabled="runBtnDisabled">
        {{runButtonText}}
      </button>
		</div>
    <div id="main-panel">
      <div id="bitfile-select">
        <input type="file" name="bitfile" id="bitfile">
        <el-button size="small" type="primary">点击上传</el-button>
      </div>
      <div id="register-list">
        <div id="input-pins">
          <h3>Input Pins</h3>
          <p>x</p>
          <input type="number" name="x" id="" v-model.number="x" @change="onx">
          <p>y</p>
          <input type="number" name="y" id="" v-model.number="y" @change="ony">
        </div>
        <div id="output-pins">
          <h3>Output Pins</h3>
          <p>z</p>
          <input type="number" name="z" id="" v-model.number="z" readonly>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as jcWebPeer from './jc-web-peer/src/index.js';

const InstrumentState = Object.freeze({
  Unconnect: "Not connected",
  Idle: "Idle",
  Connecting: "Connecting",
  Running: "Running"
})

export default {
  name: 'app',
  components: {
  },
  data: function() {
    return {
      username: 'fpgacloud',
      deviceCode: '',
      instrument: '',
      status: InstrumentState.Unconnect,
      signalServer: 'wss://sigserver.multisim.com:1339',
      runButtonText: 'Run',
      connectBtnText: 'Connect',
      runBtnDisabled: true,
      x: 1,
      y: 1,
      z: 2,
    }
  },
  methods: {
    onconnect: function() {
      if (this.status === InstrumentState.Unconnect) {
        this.connect();
      } else {
        if (this.status === InstrumentState.Running) {
          this.stop();
        }
        this.disconnect();
      }
    },
    onrun: function() {
      if (this.status === InstrumentState.Running) {
        this.stop();
        this.status = InstrumentState.Idle;
      } else {
        this.configureAndRun();
        this.status = InstrumentState.Running;
      }
    },
    connect: function() {
      this.status = InstrumentState.Connecting;
      const afterConnected = () => {
        window.console.log('Connnect succeffuly!');
        const initialDeviceCommand = {
          device: {
            device: [{
                command: 'open',
                username: this.username,
              }, {
                command: 'enumerate',
              }, {
                command: 'getDeviceInformation',
              }],
          }
        };
        this.instrument.onclose = () => {
          this.status = InstrumentState.Unconnect;
        };
        this.instrument.ondata = (p_Data) => {
          window.console.log('data: ');
          window.console.log(p_Data);
          // Do something if target send binary data
        };
        this.instrument.onresponse = (p_Response) => {
          window.console.log('response: ');
          window.console.log(p_Response);
          if (p_Response.dsdb) {
            p_Response.dsdb.dsdb.forEach((res) => {
              switch(res.command) {
              case 'open':
                if (!p_Response.hasError) {
                  this.runBtnDisabled = false;
                }
                break;
              case 'uploadBitFile':
                break;
              case 'run':
                this.runBtnDisabled = false;
                if (!p_Response.hasError) {
                  this.status = InstrumentState.Running;
                }
                break;
              case 'write':
                if (!p_Response.hasError) {
                  this.z = res.z;
                } 
                break;
              case 'close':
                if (!p_Response.hasError) {
                  this.status = InstrumentState.Idle;
                  this.runBtnDisabled = true;
                }
                break;
              }
            });
          }
        };
        this.instrument.onrestart = () => {
          this.instrument.sendCommand(initialDeviceCommand);
        };
        this.instrument.sendCommand(initialDeviceCommand);
        this.instrument.sendCommand({
          dsdb: {
            dsdb: [{
                command: 'open',
              }],
          }
        });
        this.status = InstrumentState.Idle;
      };
      this.instrument = new jcWebPeer.Device(new jcWebPeer.SignalingChannel.WebSocket(this.signalServer), this.deviceCode);
      this.instrument.connect().then(afterConnected,
        () => {
          this.status = InstrumentState.Unconnect;
          window.console.log('Failed to connect via manual connect target');
        });
    },
    disconnect: function() {
      this.instrument.disconnect();
    },
    configureAndRun: function() {
      this.runBtnDisabled = true;
      // send configure and run commands
      this.instrument.sendCommand({
        dsdb: {
          dsdb: [{
            command: 'run',
          },{
            command: 'write',
            x: this.x,
            y: this.y,
          }]
        }
      });
    },
    stop: function() {
      // send stop command
      this.instrument.sendCommand({
        dsdb: {
          dsdb: [{
            command: 'stop',
          }]
        }
      });
    },
    onx: function() {
      if(this.status === InstrumentState.Running) {
        this.instrument.sendCommand({
          dsdb: {
            dsdb: [{
              command: 'write',
              x: this.x,
              y: this.y
            }],
          }
        });
      }
    },
    ony: function() {
      if(this.status === InstrumentState.Running) {
        this.instrument.sendCommand({
          dsdb: {
            dsdb: [{
              command: 'write',
              x: this.x,
              y: this.y
            }],
          }
        });
      }
    }
  },
  watch: {
    status: function(newVal) {
      switch(newVal) {
      case InstrumentState.Idle:
        this.connectBtnText = 'Disconnect';
        this.runButtonText = 'Run';
        break;
      case InstrumentState.Running:
        this.runButtonText = 'Stop';
        break;
      case InstrumentState.Unconnect:
        this.connectBtnText = 'Connect';
        break;
      }
    }
  }
}
</script>

