/**
 * An enumeration of connection errors returned by the device.
 */
const DeviceError = Object.freeze({
	/** @type {String} */
	OperationCancelled: 'Operation cancelled',
	/** @type {String} */
	Timeout: 'Timeout',
	/** @type {String} */
	DeviceConnectionError: 'Could not connect to device',
	/** @type {String} */
	ChannelConnectionError: 'Could not connect to server',
	/** @type {String} */
	DeviceNotFound: 'Could not locate the device',
	/** @type {String} */
	CodePrefix: 'Invalid device ID prefix',
	/** @type {String} */
	DuplicatedUsername: 'Duplicated name',
});

/**
 * An enumeration of errors returned by the signaling channel
 */
const ChannelError = Object.freeze({
	/** @type {String} */
	ChannelExists: 'Signaling channel already exists',
	/** @type {String} */
	FailToCreate: 'Failed to create signaling channel',
	/** @type {String} */
	Timeout: 'Connection timed out',
	/** @type {String} */
	Canceled: 'Operation cancelled,',
});

export { DeviceError, ChannelError };
