/**
 * An object that represents a remote device
 */

/** global SignalingChannel */
import { DeviceError, ChannelError } from './errors.js';

/**
 * Represents a basic general device, providing simple generic message handling
 */
export class Device {
	/**
	 *
	 * @param {SignalingChannel} signalingChannel The signaling channel for setting up the device connection
	 * @param {String} id The id of the device
	 */
	constructor(signalingChannel, id) {
		/**
		 * The server that this device comes from
		 * @type {SignalingChannel}
		 * @private
		 */
		this._signalingChannel = signalingChannel;

		/**
		 * The semi-unique server-assigned id of the device
		 * @private
		 */
		this._id = id;

		this._internalHandlers = {
			responseHandler: (event) => {
				if (this.onresponse) {
					const jsonString = typeof event.data === 'string' ? event.data : new TextDecoder().decode(event.data);
					const message = JSON.parse(jsonString);
					this.onresponse(message);
				}
			},

			dataHandler: (event) => {
				if (this.ondata) {
					this.ondata(event.data);
				}
			},

			closeHandler: (event) => {
				if (this.onclose) {
					this.onclose(event.data);
				}
			},

			restartHandler: (event) => {
				if (this.onrestart) {
					this.onrestart(event.data);
				}
			},
		};

		/**
		 * @callback responseEventHandler
		 * @param {Object}
		 */

		/**
		 * Response handler function for responses (or messages) sent from the remote device
		 * @type {responseEventHandler}
		 */
		this.onresponse = null;

		/**
		 * @callback dataEventHandler
		 * @param {Object}
		 */

		/**
		 * Handler function for data send from the remote device
		 * @type {dataEventHandler}
		 */
		this.ondata = null;

		/**
		 * @callback restartEventHandler
		 * @param {Object}
		 */

		/**
		 * Handler function for restart
		 * @type {restartEventHandler}
		 */
		this.onrestart = null;

		/**
		 *
		 * @type {{canceled: boolean, reason: null, cancel: Device._cancelToken.cancel}}
		 * @private
		 */
		this._cancelToken = {
			canceled: false,
			reason: null,
			cancel(reason) {
				this.canceled = true;
				this.reason = reason;
				if (this.oncancel) {
					this.oncancel(reason);
				}
			},
		};
	}

	/**
	 * Get the ID of the device that uniquely identifies it on its server
	 * @returns {*} The ID of the target device for connection
	 */
	get id() { return this._id; }

	/**
	 * Connect the device object to its remote device to allow sending commands and receiving responses and data
	 * @param {Object} [options] Connection options
	 * @param {Number} [options.timeout=8000] The time (in milliseconds) each try of a connection can wait before being considered unsuccessful
	 * @param {Number} [options.retryCount=0] The number of times the connection can be retried
	 * @param {Number} [options.retryDelay=1000] The time (in milliseconds) that should elapse between retry attempts
	 * @returns {Promise} A Promise that resolves when the peer connection has been made and the channels are open
	 */
	connect(options) {
		if (!this._connectionPromise) {
			this._cancelToken.canceled = false;
			this._cancelToken.reason = null;

			this._setupRTCChannels();

			const timeout = (options && options.timeout) || 8000;
			let retryCount = (options && options.retryCount) || 0;
			const delay = (options && options.retryDelay) || 1000;

			this._controlChannel.onmessage = this._internalHandlers.responseHandler;
			this._dataChannel.onmessage = this._internalHandlers.dataHandler;
			this._deviceConnection.addEventListener('close', this._internalHandlers.closeHandler);

			// A pausing promise. Can't fail, doesn't perform any work, but adds a known delay between two Promise resolutions
			const pause = (duration) => new Promise((res) => setTimeout(res, duration));

			// Allows promises to be retried if they fail, up to the number of retries specified. Retry count is function-scoped
			// instead of a parameter to make it easy to limit the entire chain to retrying up to 'n' steps.
			const withRetry = (fn) => (...args) => {
				const retVal = fn(...args);
				if (retVal instanceof Promise) {
					return retVal.catch((err) => (retryCount-- > 1 ? pause(delay).then(withRetry(fn)) : Promise.reject(err)));
				}
			};

			const stepCancelToken = {};

			// Runs the given function only if the token's cancel function has not been called. Otherwise, throws the reason
			// for the cancel so that the promise chain can catch it.
			const withCancel = (fn, token) => (...args) => {
				if (token.canceled) {
					return Promise.reject(this._cancelToken.reason);
				}

				return new Promise((resolve, reject) => {
					token.oncancel = (reason) => {
						if (stepCancelToken.cancel) {
							stepCancelToken.cancel();
						}
						reject(reason);
					};

					fn(...args).then((result) => token.canceled || resolve(result)).catch((reason) => reject(reason));
				});
			};

			const connectSignalChannel = (signalChannelCancelToken) => this._signalingChannel.connect(signalChannelCancelToken, { timeout: 6000 }).catch((reason) => {
				if (reason === ChannelError.FailToCreate || reason === ChannelError.ChannelExists || reason === ChannelError.Timeout) {
					window.console.log(`connectSignalChannel failed with reason ${reason}`);
					return Promise.reject(DeviceError.ChannelConnectionError);
				} return Promise.reject(reason);
			});

			let timerID = -1;
			const connectionPath = (withCancel(() => connectSignalChannel(stepCancelToken), this._cancelToken))()
				.then(withCancel(() => {
					timerID = setTimeout(() => this.cancel(DeviceError.Timeout), timeout);
					return this._deviceConnection.createOffer();
				}, this._cancelToken))
				.then(withCancel((offer) => this._deviceConnection.setLocalDescription(offer), this._cancelToken))
				.then(withRetry(withCancel(() => this._negotiateConnection(stepCancelToken), this._cancelToken)))
				.then(withCancel((arg) => {
					this._signalingChannel.finalize();
					this._removeSignalingHooks();
					return Promise.resolve(arg);
				}, this._cancelToken));

			this._connectionPromise = connectionPath
				.then(withCancel((result) => {
					clearTimeout(timerID);
					return Promise.resolve(result);
				}, this._cancelToken))
				.catch((reason) => {
					clearTimeout(timerID);
					this._removeSignalingHooks();
					this._signalingChannel.disconnect();
					this._deviceConnection.close();
					this._deviceConnection = null;
					this._connectionPromise = null;

					// Convert any channel errors into device errors.
					let rejection;
					if (reason === ChannelError.Timeout) {
						rejection = Promise.reject(DeviceError.DeviceConnectionError);
					} else if (reason === ChannelError.Canceled) {
						rejection = Promise.reject(DeviceError.OperationCancelled);
					}	else {
						rejection = Promise.reject(reason);
					}
					window.console.log(`device.connect() failed with reason ${reason}`);
					return rejection;
				});
		}

		return this._connectionPromise;
	}

	/**
	 * disconnect the signaling channel
	 */
	disconnectSignalingChannel() {
		if (this._signalingChannel) {
			this._signalingChannel.disconnect();
		}
	}

	/**
	 * Disconnect from the remote device and shut down the signaling channel if it's still open
	 */
	disconnect() {
		if (this._deviceConnection) {
			this._deviceConnection.close();
			this._notifyCloseEvent();
			this._deviceConnection = null;
		}
		this.disconnectSignalingChannel();
		this._connectionPromise = null;
	}

	// eslint-disable-next-line require-jsdoc
	_notifyCloseEvent() {
		if (!this._restartTimeoutId && this._deviceConnection) {
			this._internalHandlers.closeHandler(new CloseEvent('close'));
			this._deviceConnection = null;
		}
	}

	/**
	 * Cancel the connection attempt
	 * @param {String} reason A string from the DeviceError object indicating the reason cancel was requested
	 */
	cancel(reason) {
		this._cancelToken.cancel(reason || DeviceError.OperationCancelled);
	}

	/**
	 * Send a command to the remote device
	 * @param {Object} commandInfo The command to send
	 */
	sendCommand(commandInfo) {
		if (this.connected) {
			this._controlChannel.send(new TextEncoder().encode(JSON.stringify(commandInfo)));
		}
	}

	/**
	 * Send data array Uint8Array to the remote device
	 * @param {Uint8Array} p_u8DataArray The data to send with the command
	 * @param {Object} p_command The command to send
	 */
	sendData(p_u8DataArray, p_command) {
		if (!this.connected) {
			return;
		}

		const commandU8Array = new TextEncoder().encode(JSON.stringify(p_command || {}));
		const SEPARATOR = new TextEncoder('utf-8').encode('\r\n');
		const fullU8ArrayToSend = new Uint8Array(commandU8Array.length + SEPARATOR.length + p_u8DataArray.length);
		fullU8ArrayToSend.set(commandU8Array);
		fullU8ArrayToSend.set(SEPARATOR, commandU8Array.length);
		fullU8ArrayToSend.set(p_u8DataArray, commandU8Array.length + SEPARATOR.length);

		const FRAME_SIZE = 16 * 1024;
		const arrayLength = fullU8ArrayToSend.length;

		const HEADER_SIZE = 7 * 4; // The size of "frameHeader" below
		const MAX_FRAME_DATA_SIZE = FRAME_SIZE - HEADER_SIZE;
		const frameHeader = {
			HeaderSize: HEADER_SIZE,
			DataSize: 0,
			FrameSize: FRAME_SIZE,
			TotalDataSize: arrayLength,
			TotalFrames: Math.ceil(arrayLength / MAX_FRAME_DATA_SIZE),
			FrameIndex: 0,
			DataOffset: 0,
		};

		while (frameHeader.DataOffset < arrayLength) {
			if (!this._dataChannel || this._dataChannel.readyState !== 'open') {
				break;
			}

			// Set header to U8 array
			const dataPacketArray = new Uint8Array(FRAME_SIZE);
			this._writeU32NumberToU8Array(frameHeader.HeaderSize, dataPacketArray, 0);
			this._writeU32NumberToU8Array(frameHeader.TotalDataSize, dataPacketArray, 4);
			frameHeader.DataSize = Math.min(MAX_FRAME_DATA_SIZE, arrayLength - frameHeader.DataOffset);
			this._writeU32NumberToU8Array(frameHeader.DataSize, dataPacketArray, 8);
			this._writeU32NumberToU8Array(frameHeader.FrameSize, dataPacketArray, 12);
			this._writeU32NumberToU8Array(frameHeader.TotalFrames, dataPacketArray, 16);
			this._writeU32NumberToU8Array(frameHeader.FrameIndex, dataPacketArray, 20);
			this._writeU32NumberToU8Array(frameHeader.DataOffset, dataPacketArray, 24);

			// Set the read data to U8 array
			dataPacketArray.set(fullU8ArrayToSend.slice(frameHeader.DataOffset, frameHeader.DataOffset + frameHeader.DataSize), HEADER_SIZE);

			this._dataChannel.send(dataPacketArray);

			frameHeader.FrameIndex += 1;
			frameHeader.DataOffset = frameHeader.FrameIndex * MAX_FRAME_DATA_SIZE;
		}
	}

	// eslint-disable-next-line require-jsdoc
	_writeU32NumberToU8Array(p_number, p_array, p_arrayIndex) {
		const arrayBuffer = new ArrayBuffer(4);
		const view = new DataView(arrayBuffer);
		view.setUint32(0, p_number, true); // byteOffset = 0; litteEndian = true

		p_array.set(new Uint8Array(arrayBuffer), p_arrayIndex);
		return p_array;
	}

	/**
	 * Retrieve the connected state of the WebRTC connection for this device
	 * @returns {Boolean} true if the connection has been established, false if not established or closed
	 */
	get connected() {
		// As strange as 'disconnected' is as a connected state, it's actually a transient state. If the connection
		// has truly failed it will transition from 'disconnected' to 'failed'.
		const connectedIceStates = ['connected', 'completed', 'disconnected'];

		return !!(this._deviceConnection && connectedIceStates.find((err) => err === this._deviceConnection.iceConnectionState));
	}

	/**
	 * Create the RTCPeerConnection and data channels that will be needed to negotiate the connection
	 * @private
	 */
	_setupRTCChannels() {
		/**
		 * The webrtc connection to the device
		 * @type {RTCPeerConnection}
		 * @private
		 */
		const config = { iceServers: [
			{
				urls: [
					'stun:stun.multisim.com:3478',
					'stun:stun.l.google.com:19302',
					'stun:stun1.l.google.com:19302',
					'stun:stun2.l.google.com:19302',
					'stun:stun3.l.google.com:19302',
					'stun:stun4.l.google.com:19302',
				],
			},
		] };

		// Bug fix for Firefox 57 on Windows... it warns on more than 2 stun servers and fails on more than 5
		const isFirefox = typeof InstallTrigger !== 'undefined'; // copied from 'browserdetect.js' since we cant use that here
		if (isFirefox) {
			config.iceServers.forEach((iceServer) => {
				iceServer.urls = iceServer.urls.slice(0, 2);
			});
		}

		this._deviceConnection = new RTCPeerConnection(config);
		this._deviceConnection.onicecandidate = (event) => {
			window.console.log(`onicecandidate event.candidate = ${event.candidate}`);
			if (event.candidate) {
				if (this._negotiationChannel.readyState === 'open') {
					this._negotiationChannel.send(new TextEncoder().encode(JSON.stringify(event.candidate)));
				} else {
					this._signalingChannel.send(JSON.stringify({ address: this.id, command: 'candidate', candidate: event.candidate }));
				}
			}
		};

		this._deviceConnection.oniceconnectionstatechange = (event) => {
			window.console.log(`oniceconnectionstatechange to ${event.target.iceConnectionState}`);
			switch (event.target.iceConnectionState) {
			case 'failed':
			{
				if (this._restartTimeoutId) {
					// fail again after reconnection within 60 seconds
					// I just let the connection fail and do not restart connection again.
					clearTimeout(this._restartTimeoutId);
					this._restartTimeoutId = null;
					this._internalHandlers.closeHandler(new CloseEvent('close'));
					this.disconnect();
				} else {
					this._restartTimeoutId = setTimeout(() => {
						this._restartTimeoutId = null;
					}, 60 * 1000);
					this.disconnect();
					this.connect().then(() => {
						this._internalHandlers.restartHandler(new CustomEvent('restart'));
					}).catch(() => {
						this._internalHandlers.closeHandler(new CloseEvent('close'));
						this.disconnect();
					});
				}
				break;
			}
			case 'closed':
				this._notifyCloseEvent();
				break;
			default:
				break;
			}
		};

		/**
		 * The data channel over which raw measurement/etc data will be received
		 * @type {RTCDataChannel}
		 * @private
		 */
		this._dataChannel = this._deviceConnection.createDataChannel('data');
		this._dataChannel.binaryType = 'arraybuffer';

		/**
		 * The data channel over which commands and responses will be sent/receieved
		 * @type {RTCDataChannel}
		 * @private
		 */
		this._controlChannel = this._deviceConnection.createDataChannel('control');
		this._controlChannel.binaryType = 'arraybuffer';

		/**
		 * A data channel for handling any additional peer configuration/reconfiguration after a connection is made
		 * so messages can be sent straight between peers instead of through the server.
		 * @type {RTCDataChannel}
		 * @private
		 */
		this._negotiationChannel = this._deviceConnection.createDataChannel('rtc-negotiation');
		this._negotiationChannel.binaryType = 'arraybuffer';
	}

	/**
	 * Begin the RTCPeerConnection signaling process
	 * @param {Object} cancelToken An object to support asynchronous cancel
	 * @returns {Promise} A Promise that resolves when the connection is successful, and is rejected otherwise
	 * @private
	 */
	_negotiateConnection(cancelToken) {
		return new Promise((resolve, reject) => {
			this._installSignalingHooks(resolve, reject, cancelToken);

			this._signalingChannel.send(JSON.stringify({
				address: this.id,
				command: 'offer',
				offer: this._deviceConnection.localDescription,
			}));
		});
	}

	/**
	 * Install hooks to resolve or reject a Promise based on the results of the signaling messages
	 * @param {Function} resolve Function to be called when the peer connection is opened to resolve the Promise
	 * @param {Function} reject Function to call to reject the Promise if the connection fails to open
	 * @param {Object} cancelToken And object used to allow asynchronous cancel
	 * @private
	 */
	_installSignalingHooks(resolve, reject, cancelToken) {
		const cancel = (reason) => {
			this._removeSignalingHooks();
			reject(new Error(reason));
		};

		cancelToken.cancel = cancel;
		const onChannelOpen = () => {
			if (this._dataChannel.readyState === 'open' && this._controlChannel.readyState === 'open' && this._negotiationChannel.readyState === 'open') {
				this._dataChannel.onopen = null;
				this._controlChannel.onopen = null;
				this._negotiationChannel.onopen = null;

				this._dataChannel.onerror = null;
				this._controlChannel.onerror = null;
				this._negotiationChannel.onerror = null;
				resolve();
			}
		};

		const onChannelError = (error) => {
			cancel(error);
		};

		/**
		 *
		 * @param {Object} event Object describing the message
		 * @param {*} event.address The ID of the remote peer that sent the message
		 * @param {Object} event.data The negotiation message sent by the remote peer
		 */
		this._handleSignalingMessage = (event) => {
			const message = JSON.parse(event.data);

			if (message.address === this.id || this.id === 'usb') {
				switch (message.command) {
				case 'answer':
					this._deviceConnection.setRemoteDescription(message.answer);
					break;
				case 'candidate':
					this._deviceConnection.addIceCandidate(message.candidate);
					break;
				}
			}
		};

		this._handleSignalingError = (error) => {
			if (error.reason === ChannelError.Canceled) {
				this.cancel(error.reason);
			} else {
				window.console.log(`_handleSignalingError failed with reason ${error.reason}`);
				reject(DeviceError.ChannelConnectionError);
			}
		};

		this._handleSignalingClose = (event) => {
			if (event.code === 4000) {
				this.cancel(DeviceError.DeviceNotFound);
			} else {
				window.console.log(`_handleSignalingClose failed with event ${event}, code ${event.code}`);
				this.cancel(DeviceError.ChannelConnectionError);
			}
		};

		this._signalingChannel.addEventListener('message', this._handleSignalingMessage);
		this._signalingChannel.addEventListener('error', this._handleSignalingError);
		this._signalingChannel.addEventListener('close', this._handleSignalingClose);

		this._dataChannel.onopen = onChannelOpen;
		this._controlChannel.onopen = onChannelOpen;
		this._negotiationChannel.onopen = onChannelOpen;

		this._dataChannel.onerror = onChannelError;
		this._controlChannel.onerror = onChannelError;
		this._negotiationChannel.onerror = onChannelError;
	}

	/**
	 * Remove hooks that were installed for signaling purposes
	 * @private
	 */
	_removeSignalingHooks() {
		this._signalingChannel.removeEventListener('message', this._handleSignalingMessage);
		this._signalingChannel.removeEventListener('error', this._handleSignalingError);
		this._signalingChannel.removeEventListener('close', this._handleSignalingClose);

		this._dataChannel.onopen = null;
		this._controlChannel.onopen = null;
		this._negotiationChannel.onopen = null;

		this._dataChannel.onerror = null;
		this._controlChannel.onerror = null;
		this._negotiationChannel.onerror = null;
	}
}
